import './style.css'
import { Card } from './card.js';
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'dat.gui'
import { OBJLoader} from 'three/examples/jsm/loaders/OBJLoader.js';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';

//import { FontLoader} from 'three/examples/jsm/loaders/FontLoader.js';
//import { TextGeometry } from 'three/examples/jsm/loaders/TextGeometry.js';
const white = "#e1d0bd"
const red = "#ff2e2e"
const blue = "#00b4ff"
const bomb = "#505050"

const params = {
    exposure: 10,
    bloomStrength: 1.5,
    bloomThreshold: 1,
    bloomRadius: 0
};
// Debug
const gui = new dat.GUI()

// Canvas
const canvas = document.querySelector('canvas.webgl')
const loader = new OBJLoader();
//const fontloader = new FontLoader();

// load a resource
loader.setPath('https://simonbonnedahl.dev/models/')
loader.load(
	// resource URL
	'card.obj',
	// called when resource is loaded
	function ( cardObj ) {
        cardObj.scale.set(0.0065, 0.006, 0.01)
       

        //cardObj.material = new THREE.MeshStandardMaterial()
        //cardObj.material.color = new THREE.Color(0xff0000)
        createCards(cardObj);
        

	}
);


// Scene
const scene = new THREE.Scene()

var cards = []
var colors = [blue, red, bomb, white]
function createCards(obj){
    var index = 0
    for(let x = -1.7; x < 2.5; x+= 0.85  ){
        for(let y = 1.5; y > -1; y-= 0.5){
            index ++;
            let c = new Card(obj, x, y, scene, index)
            cards.push(c)
        }
    }
    init(randomizeWords())
}   

// Objects


// Lights

const light = new THREE.AmbientLight( 0xffffff , 1); // soft white light
gui.add(light, 'intensity')
const pointlight = new THREE.PointLight(0xffffff, 0.1)
pointlight.z = 10
scene.add(pointlight)
scene.add( light );

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(1, sizes.width / sizes.height, 0.1, 1000)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 200
gui.add(camera.position, 'z')
scene.add(camera)

// Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha:true
})
//renderer.toneMapping = THREE.ReinhardToneMapping;
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

const composer = new EffectComposer( renderer );
const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
				bloomPass.threshold = params.bloomThreshold;
				bloomPass.strength = params.bloomStrength;
				bloomPass.radius = params.bloomRadius;
const renderPass = new RenderPass( scene, camera );
composer.addPass(bloomPass)
composer.addPass( renderPass)
composer.setSize(sizes.width, sizes.height)

/**
 * Animate
 */


 var devMode = false;
 var blueScore;
 var redScore;
 function init(words){
   var nRows = 5;
   var nCols = 5;
   var types =["white", "white", "white", "white", "white", "white", "white", 
   "red","red","red","red","red","red","red","red", "blue",
   "blue", "blue", "blue", "blue", "blue", "blue", "blue", "blue", "bomb"]
   for(let i =0; i<nRows; i++){
     let r = document.createElement("div");
     r.className = "row";
     for(let j =0; j<nCols; j++){   
       let type = "white"
       if(devMode){
       let index = Math.floor(Math.random()*types.length)
       type = types[index]  
       } 
       cards[(nCols*j)+i].word = words[(nCols*j)+i]
       cards[(nCols*j)+i].updateVisual(words[(nCols*j)+i], 'black', type)
       
       //b.id = (nCols*j)+i;
      // r.appendChild(b);
       }
       //grid.appendChild(r);
     }
     
   } 
   try{
   const context = cast.framework.CastReceiverContext.getInstance();
   options = new cast.framework.CastReceiverOptions();
   options.disableIdleTimeout = true;
   context.addCustomMessageListener('urn:x-cast:ch.cimnine.chromecast-cryptowords.text', function (customEvent) {
     console.log(customEvent.data)
     if (customEvent.data.type == "message") {
       if(customEvent.data.text.split(":")[0]=="init"){
         words =customEvent.data.text.split(":");
         if(words[1] == "blue"){
           redScore = 8
           blueScore = 9
         }else if(words[1] == "red"){
           redScore = 9
           blueScore = 8
         }
         document.querySelector("#scorePanel").className = ""
         updateScore();    
         words.splice(0, 2);
         console.log(words);
         if(words.length >= 24){
           init(words);
         }else{
           init(randomizeWords())
         }
         
       }else if(customEvent.data.text.split(":")[0]=="score"){
         if(customEvent.data.text.split(":")[1]=="blue"){
           blueScore--;
         }else if(customEvent.data.text.split(":")[1]=="red"){
           redScore--;
         }
         updateScore();

       }else{
         let data = customEvent.data.text.split(":");
         cards[data[0]].reveal(data[1])
         }

     }
   });
   context.start(options);
 }catch{
   }

   

   function updateScore(){
     document.querySelector("#redScore").innerHTML = redScore;
     document.querySelector("#blueScore").innerHTML = blueScore;
   }



