import { controllers } from 'dat.gui'
import * as THREE from 'three'
const white = "#e1d0bd"
const red = "#ff2e2e"
const blue = "#00b4ff"
const bomb = "#505050"
const black = "#000000"
export class Card{
    
    constructor(obj, posX, posY, scene, word){

        this.revealAnim = false 
        this.revealColor = undefined
        this.rotateSpeed = 0.3
        this.obj = obj.clone();
        this.obj.position.x = posX
        this.obj.position.y = posY

    
        this.word = word
        this.updateVisual(this.word, black, white)
        scene.add(this.obj)
        
    }
    updateVisual(text, textColor, backgroundColor){
        switch(backgroundColor){
            case "red":
                backgroundColor = red
                break
            case "blue":
                backgroundColor = blue
                break
            case "bomb":
                backgroundColor = bomb
                break
            case "white":
                backgroundColor = white
                break
           
        }
        const canvas = document.createElement("canvas");
        canvas.width = 512;
        canvas.height = 350;
        const context = canvas.getContext("2d");

        context.fillStyle = backgroundColor;
        context.fillRect(0, 0, 512, 350);

        context.font = 'Normal 64px Roboto';
          
        context.fillStyle = textColor;
        context.textAlign = "center";
        context.textBaseline = "middle";
        context.fillText(text, canvas.width/2, canvas.height/2);


        var texture = new THREE.CanvasTexture(canvas)


        this.obj.traverse( function ( child ) {       
            if ( child.isMesh ) {
                child.material = new THREE.MeshLambertMaterial()
                child.material.map = texture
            }
        } );

    }
    

    rotate(){
        this.obj.rotation.y += this.rotateSpeed;
        // if(this.obj.rotation.y > 0.9 || this.obj.rotation.y < -0.9){
        //     this.rotateSpeed = this.rotateSpeed*(-1)
        // }
        
    }
    reveal(color){
        //this.setColor(color)
        this.revealAnim = true;
        this.revealColor = color
    }
    update(){
        if(this.revealAnim){
            this.rotate()
            if(this.obj.rotation.y > 6){
                this.revealAnim = false
                if(this.revealColor === "#505050"){
                    this.updateVisual(this.word, 'white', this.revealColor)
                }else{
                this.updateVisual(this.word, black, this.revealColor)
                }
            }

        }
    }
}